$primaryColor: #4527a4;

// Dashicon
.dashicons {
  font-family: dashicons !important;
}
// Parallax Section
$scroll-factor: 1;

.wp-block-psb-parallax {
  a {
    text-decoration: none;
  }
  * {
    box-sizing: border-box;
  }

  .psbParallaxImg {
    width: 100%;
    height: 1920px;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, 0);
    pointer-events: none;
    z-index: 0;
  }

  .psbParallaxSection {
    width: 100%;
    height: 100%;
    max-height: 100%;
    z-index: 1;
    color: #fff;
    display: flex;
    flex-direction: column;

    * {
      z-index: 1;
    }

    p {
      max-width: 100%;
    }

    .block-editor-block-list__layout {
      > * {
        max-width: 100%;
      }
    }
  }
  .BPBlurEffectParallax {
    box-sizing: border-box;
    a {
      text-decoration: none;
      box-shadow: none;
    }
    box-sizing: border-box;
    .hero {
      position: relative;
      overflow: hidden;
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: left;
    }

    .hero::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      // background: rgba(0, 0, 0, 0.3);
      z-index: 1;
    }

    .hero-bg {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      // background-image: url('https://images.unsplash.com/photo-1518655048521-f130df041f66?q=80&w=2070&auto=format&fit=crop');
      background-size: cover;
      background-position: center;
      will-change: transform, filter;
      transition: filter 0.3s ease-out;
    }

    .hero-content {
      position: relative;
      z-index: 2;
      padding: 0 20px;
      max-width: 800px;
      transition:
        transform 0.3s ease-out,
        opacity 0.3s ease-out;
    }

    .hero h1 {
      // font-size: clamp(2.5rem, 5vw, 4rem);
      margin-bottom: 20px;
      // text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
      animation: fadeInUp 1s ease-out;
    }

    .hero p {
      // font-size: clamp(1rem, 2vw, 1.25rem);
      // text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
      // color: white;
      margin-bottom: 30px;
      animation: fadeInUp 1s ease-out 0.2s forwards;
      opacity: 0;
    }

    .btn {
      display: inline-block;
      padding: 12px 30px;
      // background: #ff4757;
      // color: white;
      text-decoration: none;
      border-radius: 50px;
      font-size: 16px;
      font-weight: bold;
      transition: all 0.3s ease;
      animation: fadeInUp 1s ease-out 0.4s forwards;
      opacity: 0;
      box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
      border: none;
    }

    .btn:hover {
      // background: #ff6b81;
      transform: translateY(-3px);
      box-shadow: 0 10px 25px rgba(0, 0, 0, 0.3);
      border: none;
      cursor: pointer;
    }

    .btn:focus {
      border: none;
      outline: none;
    }

    .btn:active {
      border: none;
    }

    @keyframes fadeInUp {
      from {
        opacity: 0;
        transform: translateY(20px);
      }
      to {
        opacity: 1;
        transform: translateY(0);
      }
    }

    .content {
      padding: 80px 20px;
      max-width: 1200px;
      margin: 0 auto;
      background: white;
      position: relative;
      z-index: 3;
    }

    .content h2 {
      margin-bottom: 30px;
      color: #333;
    }

    .content p {
      margin-bottom: 20px;
      line-height: 1.6;
      color: #555;
    }

    @media (min-width: 641px) and (max-width: 1024px) {
    }

    @media (max-width: 480px) {
      .btn {
        font-size: 14px;
        padding: 10px 22px;
      }
    }
  }

  .BPVerticalParallax {
    a {
      text-decoration: none;
      box-shadow: none;
    }
    .parallax-item {
      display: flex;
      align-items: center;
      justify-content: center;
      font-weight: bold;
      background-attachment: fixed !important;
      // background: url("https://templates.bplugins.com/wp-content/uploads/2025/05/Screenshot-2025-05-17-105053.png")
      // background-size: cover;
      // background-attachment: fixed; /* You can change to 'fixed' for parallax feel */
      // background-position: center center;
      // background-repeat: repeat-y;
      // font-family: Montserrat, sans-serif;
      // width: 100%;
      // min-height: 100vh;
      .text-cont {
        width: 60%;
        text-align: center;
        .title {
          // color: white;
          // font-size: 4rem;
          // font-weight: 800;
          margin: 0px;
        }
        .description {
          // color: white;
          font-weight: 400px;
          margin: 0px;
          margin-top: 12px;
        }
        .btn {
          font-size: 18px;
          // color: black;
          // background-color: white;
          padding: 12px 8px;
          border-radius: 8px;
          border: none;
          margin-top: 28px;
          transition-duration: 100ms;
          &:hover {
            cursor: pointer;
          }
          &:active {
            scale: 0.93;
            outline: none;
            border: none;
            box-shadow: none;
          }
          &:focus {
            outline: none;
          }
        }
      }
    }

    @media (min-width: 641px) and (max-width: 1024px) {
      .parallax-item {
        .text-cont {
          width: 80%;
        }
      }
    }

    @media (max-width: 480px) {
      .parallax-item {
        .text-cont {
          width: 100%;
          padding: 0px 16px;
        }
      }
    }
  }

  .bpParticleParallax {
    * {
      margin: 0;
      padding: 0;
      box-sizing: border-box;
    }

    .hero-container {
      position: relative;
      // height: 100vh;
      // background: linear-gradient(to bottom, #0f172a, #1e293b);
      overflow: hidden;
    }

    canvas#particles-canvas {
      position: absolute;
      inset: 0;
      z-index: 0;
    }

    .hero-content {
      // border: 1px solid red;
      position: relative;
      z-index: 1;
      height: 100%;
      display: flex;
      align-items: center;
      // justify-content: center !important;
      // padding: 0px 300px;
    }

    .text-content {
      max-width: 640px;
      color: white;
      text-align: left;
      transition: transform 0.2s ease;
    }

    .gradient-text {
      // background: linear-gradient(to right, #38bdf8, #34d399);
      // -webkit-background-clip: text;
      // -webkit-text-fill-color: transparent;
      // font-size: 60px;
      font-weight: 600;
    }

    .text-content h1 span.block {
      display: block;
      font-size: 55px;
      font-weight: 600;
      color: white;
    }

    .text-content p {
      margin-top: 1rem;
      font-size: 20px;
      opacity: 0.8;
    }

    .buttons {
      margin-top: 1.5rem;
      display: flex;
      gap: 1rem;
      font-size: 18px;
      flex-wrap: wrap;
    }

    .btn {
      padding: 0.75rem 1.5rem;
      border-radius: 0.5rem;
      font-weight: 600;
      cursor: pointer;
      transition: 0.3s ease;
    }

    .gradient-btn {
      // background: linear-gradient(to right, #38bdf8, #34d399);
      // color: white;
      border: none;
    }

    // .gradient-btn:hover {
    //   background: linear-gradient(to right, #0ea5e9, #10b981);
    // }

    .outline-btn {
      // background: transparent;
      border: 1px solid rgba(255, 255, 255, 0.2);
      // color: white;
    }

    // .outline-btn:hover {
    //   background: rgba(255, 255, 255, 0.05);
    // }

    .floating-cube {
      position: absolute;
      width: 150px;
      height: 150px;
      // top: 47%;
      // right: 5%;
      transform-style: preserve-3d;
      // z-index: 2;
    }

    .face {
      position: absolute;
      width: 150px;
      height: 150px;
      border: 1px solid rgba(255, 255, 255, 0.11);
      background: rgba(34, 211, 238, 0.05);
      backdrop-filter: blur(2px);
    }

    .front {
      transform: translateZ(75px);
      // background: #60caf81a;
    }
    .back {
      transform: rotateY(180deg) translateZ(75px);
      // background: rgba(0, 136, 91, 0.1);
    }
    .right {
      transform: rotateY(90deg) translateZ(75px);
      //  background: rgba(47, 255, 186, 0.1);
    }
    .left {
      transform: rotateY(-90deg) translateZ(75px);
      // background: rgba(56, 189, 248, 0.1);
    }
    .top {
      transform: rotateX(90deg) translateZ(75px);
      // background: rgba(7, 181, 255, 0.1);
    }
    .bottom {
      transform: rotateX(-90deg) translateZ(75px);
      // background: rgba(0, 241, 161, 0.1);
    }
  }

  .bplThreeImageParallax {
    * {
      box-sizing: border-box;
      margin: 0;
      padding: 0;
    }

    .parallax-hero {
      position: relative;
      height: 100vh;
      width: 100%;
      overflow: hidden;
      background: white;
    }

    /* Background layers */
    .bg-layer {
      position: absolute;
      inset: 0;
      z-index: 0;
    }

    .bg-top {
      clip-path: polygon(0 0, 100% 0, 100% 100%, 0 70%);
    }

    .bg-bottom {
      clip-path: polygon(0 65%, 100% 95%, 100% 100%, 0 100%);
    }

    /* Content */
    .hero-content {
      position: relative;
      z-index: 10;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100%;
      width: 100%;
    }

    .container {
      width: 90%;
      display: grid;
      gap: 4rem;
      grid-template-columns: 1fr 1fr;
      padding: 0 1rem;
    }

    .text-section {
      display: flex;
      flex-direction: column;
      justify-content: center;
    }

    .badge {
      background-color: #d1fae5;
      color: #065f46;
      font-size: 14px;
      font-weight: 600;
      padding: 6px 12px;
      border-radius: 999px;
      margin-bottom: 1rem;
      display: inline-block;
    }

    h1 {
      font-size: 3rem;
      // color: #111827;
      font-weight: 800;
      margin-bottom: 1rem;
      line-height: 1.2;
    }

    .sub-title {
      font-size: 2.6rem;
      display: block;
      // color: #16a34a;
      font-weight: 800;
      margin-bottom: 1rem;
      line-height: 1.2;
    }

    p {
      font-size: 1.125rem;
      color: #4b5563;
      margin-bottom: 2rem;
    }

    .buttons {
      display: flex;
      gap: 1rem;
    }

    .btn {
      padding: 12px 20px;
      font-size: 1rem;
      border-radius: 6px;
      cursor: pointer;
      border: none;
      display: inline-flex;
      align-items: center;
    }

    // .btn.primary {
    //   background-color: #16a34a;
    //   color: white;
    // }

    // .btn.primary:hover {
    //   background-color: #15803d;
    // }

    .btn.outline {
      border: 2px solid #bbf7d0;
      // color: #16a34a;
      // background: transparent;
    }

    // .btn.outline:hover {
    //   background-color: #f0fdf4;
    // }

    /* Images */
    .image-section {
      position: relative;
    }

    // @media (min-width: 768px) {
    //   .image-section {
    //     display: block;
    //   }
    // }

    .main-image {
      position: relative;
      height: 500px;
      width: 100%;
      overflow: hidden;
      border-radius: 16px;
    }

    .main-image img {
      object-fit: cover;
      width: 100%;
      height: 100%;
    }

    .product-card {
      position: absolute;
      width: 180px;
      height: 120px;
      background-color: white;
      border-radius: 12px;
      padding: 12px;
      box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    }

    .left-card {
      top: 20%;
      left: -10%;
      transform: rotate(-5deg);
    }

    .right-card {
      top: 65%;
      left: 85%;
      transform: rotate(8deg);
    }

    .product-image {
      height: 60px;
      width: 100%;
      overflow: hidden;
      border-radius: 6px;
    }

    .product-image img {
      object-fit: cover;
      width: 100%;
      height: 100%;
    }

    .product-text {
      margin-top: 8px;
    }

    .product-text .label {
      font-size: 12px;
      color: #16a34a;
      font-weight: 500;
    }

    .product-text .title {
      font-size: 14px;
      font-weight: 700;
      color: #111827;
    }

    /* Scroll indicator */
    .scroll-indicator {
      position: absolute;
      bottom: 2rem;
      left: 50%;
      transform: translateX(-50%);
      animation: bounce 1.5s infinite;
      z-index: 20;
    }

    @keyframes bounce {
      0%,
      100% {
        transform: translateX(-50%) translateY(0);
      }

      50% {
        transform: translateX(-50%) translateY(-10px);
      }
    }

    .chevron-down {
      stroke: #16a34a;
    }

    /* Leaves */
    #leaves-container svg {
      position: absolute;
      z-index: 0;
    }

    @media (min-width: 641px) and (max-width: 1024px) {
      .container {
        display: grid;
        grid-template-columns: 1fr;
        padding: 12px;
      }
      .hero-content {
        display: flex;
      }
      .parallax-hero {
        height: auto;
      }
      .image-section {
        margin-top: 0;
      }
    }

    @media (max-width: 480px) {
      .container {
        display: block;
        padding: 2.5rem 1rem;
        width: 100%;
      }
      .hero-content {
        display: block;
      }
      .parallax-hero {
        height: auto;
      }
      .image-section {
        margin-top: 56px;
      }
    }
  }

  .bplScrolingParallax {
    margin: 0;
    font-family: Arial, sans-serif;
    background: rgb(124, 10, 255);
    color: white;
    overflow-x: hidden;
    position: relative;
    z-index: 1;
    display: flex;
    align-items: center;

    .header {
      text-align: center;
      padding: 5rem 1rem;
    }

    .header h1 {
      font-size: 3.5rem;
      font-weight: bold;
      margin-bottom: 1rem;
    }

    .header p {
      font-size: 1.25rem;
      color: #9ca3af;
    }

    .parallax-container {
      display: flex;
      flex-direction: column;
      gap: 2rem;
      padding-bottom: 5rem;
    }

    .parallax-row {
      position: relative;
      z-index: 1;
      display: flex;
      // gap: 1.5rem;
      width: max-content;
      will-change: transform;
    }

    .card {
      position: relative;
      // width: 20rem;
      // height: 12rem;
      border-radius: 0.5rem;
      overflow: hidden;
      background: #1f2937;
      flex-shrink: 0;
      cursor: pointer;
      transition: transform 0.3s ease;
    }

    .card:hover {
      transform: scale(1.05);
    }

    .card img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    .card::after {
      content: "";
      position: absolute;
      inset: 0;
      background: linear-gradient(
        to top,
        rgba(0, 0, 0, 0.8),
        transparent,
        transparent
      );
    }

    .card-content {
      position: absolute;
      bottom: 1rem;
      left: 1rem;
      z-index: 2;
    }

    .card-content h3 {
      font-size: 1.125rem;
      font-weight: bold;
      margin: 0;
    }

    .card-content p {
      font-size: 0.875rem;
      color: #d1d5db;
      margin: 0.25rem 0 0;
    }

    .footer {
      height: 100vh;
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
    }

    .footer h2 {
      font-size: 2.25rem;
      font-weight: bold;
      margin-bottom: 1rem;
    }

    .footer p {
      color: #9ca3af;
    }
    /* ===== Overlay ===== */
    .modal-overlay {
      position: fixed;
      inset: 0;
      background: rgba(0, 0, 0, 0.85);
      display: flex;
      justify-content: center;
      align-items: center;
      z-index: 9999;
      animation: fadeIn 0.3s ease forwards;
      padding: 16px; /* ensures modal doesn't touch edges on small screens */
    }

    /* ===== Container ===== */
    .modal-container {
      position: relative;
      max-width: 900px;
      width: 100%;
      border-radius: 16px;
      padding: 24px;
      box-shadow: 0 12px 48px rgba(0, 0, 0, 0.6);
      animation: slideUp 0.35s ease forwards;
      overflow: hidden;
    }

    /* ===== Close Button ===== */
    .modal-close {
      position: absolute;
      top: 14px;
      right: 14px;
      background: transparent;
      // color: white;
      border: none;
      font-size: 38px;
      cursor: pointer;
      transition:
        transform 0.2s ease,
        color 0.2s ease;
      -webkit-text-stroke: 1px black;
    }

    .modal-close:hover {
      transform: scale(1.15);
      // color: #ff4d4d !important;
    }

    /* ===== Image ===== */
    .modal-image {
      width: 100%;
      max-height: 70vh;
      object-fit: contain;
      border-radius: 10px;
      display: block;
      margin: 0 auto;
    }

    /* ===== Text ===== */
    .modal-text {
      margin-top: 20px;
      color: #f5f5f5;
    }

    .modal-text h2 {
      font-size: clamp(20px, 2.5vw, 28px);
      margin-bottom: 4px; /* tighter spacing */
      font-weight: 700;
    }

    .modal-text p {
      font-size: clamp(14px, 2vw, 18px);
      line-height: 1.5;
      opacity: 0.9;
      margin-bottom: 8px;
    }

    .btn{
      outline: none;
      border: none;
      font-size: 16px;
      padding: 6px 10px;
      border-radius: 2px;
      cursor: pointer;
      transition: all 0.5s ease;
    }

    /* ===== Responsive Tweaks ===== */
    @media (max-width: 768px) {
      .modal-container {
        padding: 16px;
        border-radius: 12px;
      }

      .modal-text h2 {
        font-size: 22px;
      }

      .modal-text p {
        font-size: 16px;
      }
    }

    @media (max-width: 480px) {
      .modal-container {
        padding: 14px;
      }

      .modal-close {
        font-size: 24px;
        top: 10px;
        right: 10px;
      }

      .modal-text h2 {
        font-size: 20px;
      }

      .modal-text p {
        font-size: 15px;
      }
    }

    /* Animations */
    @keyframes fadeIn {
      from {
        opacity: 0;
      }
      to {
        opacity: 1;
      }
    }

    @keyframes slideUp {
      from {
        transform: translateY(300px);
        opacity: 0;
      }
      to {
        transform: translateY(0);
        opacity: 1;
      }
    }
  }
}
